import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';
import Toast from './Toast';

function Interconsultas(
  { viewinterconsulta,
    hospital,
    unidade,
    idusuario,
    usuario,
    tipo,
    funcao,
    idpaciente,
    idatendimento,
    idinterconsulta,
    pedido,
    especialidade,
    motivo,
    status,
    parecer,
  }) {
  //servidor.
  var html = 'https://pulsarapp-server.herokuapp.com';
  const changeState = useDispatch();

  // chave para exibição do componente.
  const [viewcomponent, setviewcomponent] = useState(viewinterconsulta);

  useEffect(() => {
    if (viewinterconsulta !== 0) {
      setviewcomponent(viewinterconsulta);
      loadEspecialidades();
      if (viewinterconsulta === 1) {
        setselectedespecialidade('SELECIONE UMA ESPECIALIDADE');
      } else {
        setselectedespecialidade(especialidade);
      }
    } else {
    }
  }, [viewinterconsulta])

  // carregando lista de especialidades.
  const [especialidades, setespecialidades] = useState([]);
  const loadEspecialidades = () => {
    axios.get(html + "/especialidades").then((response) => {
      setespecialidades(response.data);
    });
  }

  // tela para seleção da especialidade.
  const [showespecialidades, setshowespecialidades] = useState(0);
  function ShowEspecialidades() {
    if (showespecialidades === 1) {
      return (
        <div className="menucover" style={{ zIndex: 9, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          <div className="menucontainer">
            <div className="scroll"
              id="LISTA DE ESPECIALIDADES"
              style={{
                display: 'flex',
                justifyContent: 'flex-start',
                margin: 5,
                padding: 0,
                paddingRight: 10,
                width: 0.4 * window.innerWidth,
                height: 0.3 * window.innerHeight,
              }}
            >
              <div style={{ width: '100%' }}>
                {especialidades.map((item) => (
                  <button
                    className="blue-button"
                    style={{
                      width: '100%',
                      padding: 10,
                      margin: 5,
                    }}
                    onClick={() => selectEspecialidade(item.especialidade)}
                  >
                    {selectedespecialidade}
                  </button>
                ))}
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // selecionando uma especialidade.
  const selectEspecialidade = (value) => {
    setselectedespecialidade(value);
    setshowespecialidades(0);
  }

  // inserindo registro.
  const [selectedespecialidade, setselectedespecialidade] = useState();
  const insertData = () => {
    var motivo = document.getElementById("inputMotivo").value.toUpperCase();
    if (motivo !== '' && selectedespecialidade !== 'SELECIONE UMA ESPECIALIDADE') {
      var obj = {
        idpaciente: idpaciente,
        hospital: hospital,
        unidade: unidade,
        idatendimento: idatendimento,
        pedido: moment().format('DD/MM/YY - HH:mm'),
        especialidade: selectedespecialidade,
        motivo: motivo,
        status: 0,
        parecer: ''
      };
      axios.post(html + '/insertinterconsulta', obj).then(() => {
        toast(1, '#52be80', 'INTERCONSULTA REGISTRADA COM SUCESSO.', 5000);
        setTimeout(() => {
          fechar();
        }, 3000);
      });
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 5000);
    }
  };

  // atualizando registro.
  const updateData = () => {
    var motivo = document.getElementById("inputMotivo").value.toUpperCase();
    if (motivo !== '' && selectedespecialidade !== '') {
      var obj = {
        idpaciente: idpaciente,
        hospital: hospital,
        unidade: unidade,
        idatendimento: idatendimento,
        pedido: pedido,
        especialidade: selectedespecialidade,
        motivo: motivo,
        status: status,
        parecer: parecer,
      };
      axios.post(html + '/updateinterconsulta/' + idinterconsulta, obj).then(() => {
        toast(1, '#52be80', 'INTERCONSULTA ATUALIZADA COM SUCESSO.', 5000);
        setTimeout(() => {
          fechar();
        }, 3000);
      });
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 5000);
    }
  };

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  const fechar = () => {
    changeState({
      type: 'CORRIDA_OFF',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
    changeState({
      type: 'CORRIDA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
  }

  // renderização do componente.
  if (viewcomponent === 1) { // inserir.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 0,
          zIndex: 5,
        }}
      >
        <ShowEspecialidades></ShowEspecialidades>
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0 }}>
            INSERIR INTERCONSULTA
          </label>
          <button
            className="blue-button"
            style={{
              width: 300,
              padding: 10,
            }}
            onClick={() => setshowespecialidades(1)}
          >
            {selectedespecialidade}
          </button>
          <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
            MOTIVO:
          </label>
          <textarea
            autoComplete="off"
            className="textarea"
            placeholder="MOTIVO."
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'MOTIVO.')}
            title="MOTIVO DA INTERCONSULTA."
            style={{
              width: 0.4 * window.innerWidth,
              minWidth: 400,
              minHeight: 125,
            }}
            type="text"
            maxLength={200}
            id="inputMotivo"
          />
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 25,
              marginBottom: 0,
            }}
          >
            <button className="green-button" title="ASSINAR PEDIDO DE INTERCONSULTA." onClick={() => insertData()}>
              <img
                alt=""
                src={salvar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <button className="red-button" tile="EXCLUIR PEDIDO DE INTERCONSULTA." onClick={() => setviewcomponent(0)}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else if (viewcomponent === 2) { // atualizar.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 0,
          zIndex: 5,
        }}
      >
        <ShowEspecialidades></ShowEspecialidades>
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0 }}>
            EDITAR INTERCONSULTA
          </label>
          <button
            className="blue-button"
            style={{
              width: 250,
              padding: 10,
            }}
            onClick={() => setshowespecialidades(1)}
          >
            {selectedespecialidade}
          </button>
          <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
            MOTIVO:
          </label>
          <textarea
            autoComplete="off"
            className="textarea"
            placeholder="MOTIVO."
            defaultValue={especialidade}
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'MOTIVO.')}
            title="MOTIVO DA INTERCONSULTA."
            style={{
              width: 0.4 * window.innerWidth,
              minWidth: 400,
              minHeight: 125,
            }}
            type="text"
            maxLength={200}
            id="inputMotivo"
            defaultValue={motivo}
          />
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 25,
              marginBottom: 0,
            }}
          >
            <button className="green-button" onClick={() => updateData()}>
              <img
                alt=""
                src={salvar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <button className="red-button" onClick={() => setviewcomponent(0)}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Interconsultas;