import React, { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import { Doughnut } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import logo from '../images/logo.png';
import editar from '../images/editar.svg';
import salvar from '../images/salvar.svg';
import deletar from '../images/deletar.svg';
import Toast from './Toast';

function Unidades() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewUnidades = useSelector((state) => state.unidades);
  const [cleanrender, setcleanrender] = useState(0);

  // recuperando estados atualizados.
  const idusuario = useSelector((state) => state.idusuario);
  const usuario = useSelector((state) => state.nomeusuario);
  const funcao = useSelector((state) => state.funcao);
  const tipo = useSelector((state) => state.tipo);
  const hospital = useSelector((state) => state.nomehospital);

  // recuperando dados do usuário.
  const [usuarionome, setusuarionome] = useState(null);
  const [usuariofirstname, setusuariofirstname] = useState(null);
  const [usuariolastname, setusuariolastname] = useState(null);
  const [usuarioconselho, setusuarioconselho] = useState(null);
  const [usuariofuncao, setusuariofuncao] = useState(null);
  const [usuarioespecialidade, setusuarioespecialidade] = useState(null);
  const getUser = () => {
    axios.get(html + "/getusuario/'" + idusuario + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      setusuarionome(x.map((item) => item.nome));
      setusuariofirstname(x.map((item) => item.firstname));
      setusuariolastname(x.map((item) => item.lastname));
      setusuarioconselho(x.map((item) => item.conselho));
      setusuariofuncao(x.map((item) => item.funcao));
      setusuarioespecialidade(x.map((item) => item.especialidade));
    });
  }

  // carregamento da lista de unidades de atendimento do hospital, nas quais o usuário trabalha.
  const [unidades, setUnidades] = useState([]);
  const loadUnidades = () => {
    // ROTA: SELECT * FROM usuarioxhospitalxunidade WHERE idusuario = idusuario AND hospital = hospital.
    axios.get(html + "/unidades/'" + idusuario + "'/'" + hospital + "'").then((response) => {
      setUnidades(response.data);
    });
  }
  // montando a lista de unidades.
  function ShowUnidades() {
    return (
      <div
        style={{
          display: 'flex',
          minWidth: window.innerWidth,
          width: window.innerWidth,
          paddingLeft: 5,
          paddingRight: 5,
        }}>
        <div
          className="scroll"
          id="LISTA DE UNIDADES"
          style={{
            display: 'flex',
            width: '100%',
            height: '70vh',
            flexDirection: 'row',
            flexWrap: 'wrap',
            justifyContent: 'center',
            alignItems: 'center',
            borderRadius: 5,
            margin: 5,
            paddingRight: 5,
          }}
        >
          {unidades.map((item) => (
            GetData(item)
          ))}
        </div>
      </div>
    );
  }

  // selecionando uma unidade da lista.
  const selectUnidade = (item) => {
    if (tipo !== 3) {
      changeState({
        type: 'PACIENTES_ON',
        // payloads.
        payloadIdusuario: idusuario,
        payloadNomeusuario: usuario,
        payloadFuncao: funcao,
        payloadTipo: tipo,
        payloadTipounidade: item.pa, // indica se a unidade é pronto-atendimento.
        payloadNomehospital: item.hospital,
        payloadNomeunidade: item.unidade,
      });
    } else {
    }
  }

  useEffect(() => {
    if (viewUnidades === 1) {
      setcleanrender(0);
      // carregando a especialidade do usuário.
      getUser();
      // carregando a lista de unidades.
      loadUnidades()
      // carregando as interconsultas.
      loadInterconsultas();
      setTimeout(() => {
        setcleanrender(1);
      }, 1000);
    }
  }, [viewUnidades]);

  const selectEscala = (item) => {
    changeState({
      type: 'ESCALA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
      payloadNomehospital: item.hospital,
      payloadNomeunidade: item.unidade,
    });
  }

  // botão para opção de movimentação de paciente (abre o componente 'SECRETÁRIA'), para cada unidade de atendimento.
  function ShowSecretaria() {
    return (
      <button className="blue-button"
        style={{ width: 150, height: 70, margin: 2.5, display: funcao === 'SEC' || funcao === 'ENF' ? 'flex' : 'none' }}
        onClick={() => { selectSecretaria() }}
      >
        {'MOVIMENTAR PACIENTE'}
      </button>
    );
  }
  const selectSecretaria = () => {
    changeState({
      type: 'ATENDIMENTO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
      payloadNomehospital: hospital
    });
  }

  // abrindo a lista de interconsultas.
  function showInterconsultas(event) {
    loadInterconsultas();
    setviewinterconsultas(1);
    event.stopPropagation();
  }

  // botão para acesso a solicitação de INTERCONSULTAS, para a especialidade do usuário.
  function BtnInterconsultas(hosp, setor) {
    return (
      <button className="red-button"
        title={"INTERCONSULTAS PENDENTES OU PACIENTES EM \n ACOMPANHAMENTO PELA " + usuarioespecialidade + '.'}
        style={{
          position: 'absolute',
          bottom: 10,
          right: 10,
          backgroundColor: '#ec7063',
          borderStyle: 'solid',
          borderWidth: 5,
          borderColor: '#e1e5f2',
          borderRadius: 50, width: 50, height: 50, margin: 0, padding: 5, opacity: 1,
          display: usuariofuncao == 'MED' && listinterconsultas.filter((item) => item.status !== 0 && item.especialidade == usuarioespecialidade && item.hospital === hosp && item.unidade === setor).length > 0 ? 'flex' : 'none',
        }}
        onClick={(e) => showInterconsultas(e)}
      >
        {listinterconsultas.filter((item) => item.status !== 0 && item.especialidade == usuarioespecialidade && item.hospital === hosp && item.unidade === setor).length}
      </button>
    );
  }

  // função para seleção de uma interconsulta.
  const [idpaciente, setidpaciente] = useState(0);
  const [idinterconsulta, setidinterconsulta] = useState(0);
  const [pedidointerconsulta, setpedidointerconsulta] = useState('');
  const [especialidadeinterconsulta, setespecialidadeinterconsulta] = useState('');
  const [motivointerconsulta, setmotivointerconsulta] = useState('');
  const [statusinterconsulta, setstatusinterconsulta] = useState(0);
  const [parecerinterconsulta, setparecerinterconsulta] = useState('');
  const selectinterconsulta = (item) => {
    setidinterconsulta(item.id);
    setidpaciente(item.idpaciente);
    setpedidointerconsulta(item.pedido);
    setespecialidadeinterconsulta(item.especialidade);
    setmotivointerconsulta(item.motivo);
    setstatusinterconsulta(item.status);
  }

  const [showparecer, setshowparecer] = useState(0);
  // lista de interconsultas não atendidas para a especialidade do usuário.
  const [listinterconsultas, setlistinterconsultas] = useState([]);
  const loadInterconsultas = () => {
    axios.get(html + "/interconsultasall").then((response) => {
      var x = [0, 1];
      x = response.data;
      setlistinterconsultas(x);
    });
  }

  var newparecer = '';
  const updateParecer = (value) => {
    newparecer = value;
  }

  // atualizando um pedido de interconsulta.
  const updateInterconsulta = (item, value) => {
    var obj = {
      idpaciente: item.idpaciente,
      idatendimento: item.idatendimento,
      pedido: item.pedido,
      especialidade: item.especialidade,
      motivo: item.motivo,
      status: value,
      parecer: value == 2 ? newparecer : item.parecer,
    };
    axios.post(html + '/updateinterconsulta/' + item.id, obj).then(() => {
      toast(1, '#52be80', 'INTERCONSULTA RESPONDIDA COM SUCESSO.', 3000);
      loadInterconsultas();
    });
  }

  // acessando o prontuário do paciente com interconsulta.
  const gotoProntuario = (item) => {
    setviewinterconsultas(0);
    changeState({
      type: 'CORRIDA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: item.hospital,
      payloadNomeunidade: item.unidade,
      payloadIdatendimento: item.idatendimento,
    });
  }

  // memorizando a posição da scroll nas listas.
  var scrollpos = 0;
  const [scrollposition, setscrollposition] = useState(0);
  var timeout;
  const scrollPosition = (value) => {
    clearTimeout(timeout);
    timeout = setTimeout(() => {
      scrollpos = document.getElementById(value).scrollTop;
      setscrollposition(document.getElementById(value).scrollTop);
      document.getElementById(value).scrollTop = scrollpos;
    }, 500);
  }

  // guardando a posição da scroll.
  const keepScroll = (value) => {
    setTimeout(() => {
      document.getElementById(value).scrollTop = scrollposition;
    }, 300);
  }

  // exibindo a tela de interconsultas.
  const [viewinterconsultas, setviewinterconsultas] = useState(0);
  function ViewInterconsultas() {
    if (viewinterconsultas === 1) {
      return (
        <div className="menucover" style={{ zIndex: 3, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          <div className="menucontainer">
            <div className="title2">{'LISTA DE INTERCONSULTAS: ' + hospital}</div>
            <div
              className="scroll"
              id="LISTA DE INTERCONSULTAS"
              onScroll={() => scrollPosition('LISTA DE INTERCONSULTAS')}
              onLoad={() => keepScroll('LISTA DE INTERCONSULTAS')}
              style={{
                display: 'flex',
                justifyContent: 'flex-start',
                margin: 5,
                padding: 2.5,
                height: (0.5 * window.innerHeight) + 30,
                minWidth: 0.90 * (window.innerWidth),
                maxWidth: 0.90 * (window.innerWidth),
              }}
            >
              {listinterconsultas.filter((item) => item.status !== 0).map((item) => (
                <div
                  key={item.id}
                  id="item da lista"
                  className="row"
                  style={{ margin: 2.5, marginTop: 2.5, marginBottom: 2.5, display: 'flex', flexDirection: 'column', justifyContent: 'center' }}
                >
                  <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', width: '100%', paddingLeft: 5, paddingRight: 5 }}>
                    <button
                      className="blue-button"
                      style={{
                        width: 100,
                        minWidth: 100,
                        margin: 2.5,
                        padding: 5,
                        flexDirection: 'column',
                        backgroundColor: '#1f7a8c',
                      }}
                    >
                      {item.pedido.substring(0, 8)}
                    </button>
                    <button
                      className="blue-button"
                      style={{
                        width: 200,
                        minWidth: 200,
                        margin: 2.5,
                        padding: 5,
                        flexDirection: 'column',
                        backgroundColor: '#1f7a8c',
                      }}
                    >
                      {item.unidade}
                    </button>
                    <button
                      className="blue-button"
                      style={{
                        width: '100%',
                        margin: 2.5,
                        padding: 5,
                        flexDirection: 'column',
                        backgroundColor: '#1f7a8c',
                      }}
                    >
                      {item.especialidade}
                    </button>
                    <button
                      className="blue-button"
                      title="STATUS DA INTERCONSULTA. CLIQUE PARA ACESSAR O PRONTUÁRIO."
                      onClick={item.status === 1 ? () => gotoProntuario(item) : ''}
                      style={{
                        width: 260,
                        margin: 2.5,
                        padding: 5,
                        flexDirection: 'column',
                        backgroundColor: item.status === 0 || item.status === 1 ? '#ec7063' : item.status === 2 ? '#f5b041' : '#52be80',
                      }}
                    >
                      {item.status === 0 || item.status === 1 ? 'PENDENTE' : item.status === 2 ? 'ACOMPANHANDO' : 'ENCERRADO'}
                    </button>
                    <button className="green-button"
                      onClick={() => updateInterconsulta(item, 2)}
                      title={item.status === 1 ? "SALVAR PARECER E ACOMPANHAR PACIENTE." : "INTERCONSULTA JÁ RESPONDIDA."}
                      style={{
                        opacity: item.status !== 0 && item.status !== 1 ? 0.5 : 1,
                      }}
                    >
                      <img
                        alt=""
                        src={salvar}
                        style={{
                          margin: 10,
                          height: 30,
                          width: 30,
                        }}
                      ></img>
                    </button>
                    <button className="red-button"
                      onClick={() => updateInterconsulta(item, 3)}
                      title="ENCERRAR A INTERCONSULTA (ALTA)."
                      style={{
                        opacity: item.status === 2 ? 1 : 0.5,
                        marginRight: -2.5
                      }}
                    >
                      <img
                        alt=""
                        src={deletar}
                        style={{
                          margin: 10,
                          height: 30,
                          width: 30,
                        }}
                      ></img>
                    </button>
                  </div>
                  <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', width: '100%', paddingLeft: 5, paddingRight: 5 }}>
                    <button className="blue-button"
                      style={{
                        flexDirection: 'row',
                        justifyContent: 'left',
                        width: '100%',
                        padding: 10,
                        margin: 2.5,
                      }}
                    >
                      {'MOTIVO: ' + item.motivo}
                    </button>
                    <textarea
                      className="textarea"
                      title="PARECER DO ESPECIALISTA."
                      id="inputParecer"
                      onKeyUp={(e) => updateParecer(e.target.value)}
                      style={{
                        flexDirection: 'row',
                        justifyContent: 'left',
                        width: '100%',
                        padding: 10,
                        margin: 2.5,
                      }}
                      type="text"
                      maxLength={200}
                      defaultValue={item.parecer}
                    ></textarea>
                  </div>
                </div>
              ))}
            </div>
            <button className="red-button"
              onClick={() => setviewinterconsultas(0)}
              style={{ marginTop: 20 }}
            >
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div >
      )
    } else {
      return null;
    }
  }

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time);
  }

  // CHART.
  /* gráfico em torta que exibe o total de leitos vagos e o total
  de leitos ocupados para cada unidade. */
  var pacientes = 0;
  var leitos = 0;
  var dataChart = [];
  function GetData(item) {
    const [pct, setpct] = useState(0);
    const [lto, setlto] = useState(0);
    // obtendo o total de pacientes internados na unidade.
    // ROTA: SELECT * FROM atendimentos WHERE hospital = item.hospital AND unidade = item.unidade.
    axios.get(html + "/pacientesunidade/'" + item.hospital + "'/'" + item.unidade + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      pacientes = x.length;
      setpct(pacientes);
    });
    // obtendo o total de leitos para a unidade.
    // ROTA: SELECT * FROM hospitaisxunidades WHERE hospital = item.hospital AND unidade = item.unidade.
    axios.get(html + "/totalleitosunidade/'" + item.hospital + "'/'" + item.unidade + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      const arrayleitos = x.map((item) => item.leitos);
      // efetuando a soma de leitos.
      function somaLeitos(total, num) {
        return total + num;
      }
      leitos = arrayleitos.reduce(somaLeitos);
      setlto(leitos);
    });
    // gerando os dados do gráfico.
    console.log('LEITOS: ' + lto + ', PACIENTES: ' + pct + '.');
    dataChart = {
      labels: [' LIVRES', ' OCUPADOS'],
      datasets: [
        {
          data: [lto - pct, pct],
          backgroundColor: [
            '#52be80',
            '#ec7063',
          ],
          borderColor: '#E1E5F2',
          hoverBorderColor: ['#E1E5F2', '#E1E5F2'],
        },
      ],
    };
    return (
      <div>
        <div
          className="card"
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            borderRadius: 5,
            padding: 10,
            margin: 5,
          }}
        >
          <p className="title2"
            style={{
              fontSize: 22,
              fontWeight: 'bold',
              margin: 10,
              padding: 0,
            }}>
            {item.unidade}
          </p>
          <div style={{ position: 'relative' }}
            onClick={() => selectUnidade(item)}>
            <Doughnut
              data={dataChart}
              width={200}
              height={200}
              plugins={ChartDataLabels}
              options={{
                plugins: {
                  datalabels: {
                    display: function (context) {
                      return context.dataset.data[context.dataIndex] !== 0;
                    },
                    color: '#FFFFFF',
                    font: {
                      weight: 'bold',
                      size: 16,
                    },
                  },
                },
                tooltips: {
                  enabled: false,
                },
                hover: { mode: null },
                elements: {
                  arc: {
                    hoverBorderColor: '#E1E5F2',
                    borderColor: '#E1E5F2',
                    borderWidth: 5,
                  },
                },
                animation: {
                  duration: 0,
                },
                title: {
                  display: false,
                  text: 'OCUPAÇÃO DE LEITOS',
                },
                legend: {
                  display: false,
                  position: 'bottom',
                },
                maintainAspectRatio: true,
                responsive: false,
              }}
            />
            {BtnInterconsultas(item.hospital, item.unidade)};
          </div>
          <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
            <div>
              <p
                className="title2"
                style={{
                  fontSize: 16,
                  fontWeight: 'bold',
                  margin: 2.5,
                  marginTop: 10,
                  padding: 0,
                }}
              >
                {'OCUPAÇÃO: ' + Math.ceil(pct * 100 / lto) + '%'}
              </p>
            </div>
            <div
              className="secondary"
              style={{
                flexDirection: 'row',
                marginTop: 5,
                marginBottom: 10,
              }}
            >
              <div
                id="VAGOS"
                style={{
                  width: 20,
                  height: 20,
                  borderRadius: 5,
                  backgroundColor: '#52be80',
                  margin: 2.5,
                  padding: 0,
                }}
              ></div>
              <p
                className="title2"
                style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
              >
                VAGOS
          </p>
              <div
                id="OCUPADOS"
                style={{
                  width: 20,
                  height: 20,
                  borderRadius: 5,
                  backgroundColor: '#ec7063',
                  margin: 2.5,
                  padding: 0,
                }}
              ></div>
              <p
                className="title2"
                style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
              >
                OCUPADOS
          </p>
            </div>
            {
              Escala(item)
            }
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', marginTop: 0 }}>
              <button
                className="blue-button"
                onClick={() => clickAcolhimento(item)}
                style={{
                  display: tipo === 5 ? 'flex' : 'none',
                  minWidth: 150,
                  height: 70,
                  margin: 2.5,
                  padding: 10,
                  color: '#ffffff',
                  alignSelf: 'center',
                }}
                title="ACOLHIMENTO"
              >
                ACOLHIMENTO
            </button>
              <ShowSecretaria></ShowSecretaria>
            </div>
          </div>
        </div>
      </div>
    );
  }

  function Escala(item) {
    if (tipo === 2 || tipo === 3) { // tipos 2 (gestor) e 3 (secretária).
      return (
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', width: '100%' }}>
          <button className="blue-button" style={{ width: 150, alignSelf: 'center' }} onClick={() => selectEscala(item)}>
            {'ESCALA'}
          </button>
        </div>
      )
    } else {
      return null;
    }
  }

  // abrindo a tela de acolhimento (classificação de risco).
  const clickAcolhimento = (item) => {
    changeState({
      type: 'ACOLHIMENTO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: item.unidade,
    });
  };

  // HEADER.
  const clickLogin = () => {
    changeState({ type: 'LOGIN_ON' });
  };
  // função para retornar à tela Login.
  const clickVoltar = () => {
    changeState({
      type: 'HOSPITAL_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
    });
  };
  // função para acessar a tela Password.
  const clickPassword = () => {
    changeState({
      type: 'PASSWORD_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
    });
  };
  // function Header.
  function Header() {
    return (
      <div
        id="HEADER"
        style={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'center',
          alignItems: 'center',
          margin: 5,
          padding: 5,
          width: '100%',
          maxHeight: '20vh',
        }}
      >
        <div
          className="secondary"
          style={{
            display: 'flex',
            flex: 1,
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <img
            alt=""
            src={logo}
            style={{
              marginTop: 15,
              marginBottom: 15,
              marginLeft: -5,
              marginRight: -5,
              height: '90%',
            }}
          ></img>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            flex: 7,
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <div style={{ display: 'flex', flexDirection: 'row' }}>
            <p className="title1" style={{ color: '#1F7A8C' }}>
              {'LISTA DE UNIDADES: '}
            </p>
            <p className="title1" style={{ color: '#ec7063' }}>
              {hospital}
            </p>
          </div>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            flexDirection: 'column',
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <button
            className="blue-button"
            title="CLIQUE PARA ALTERAR SUA SENHA."
            onClick={() => clickPassword()}
            style={{
              display: 'flex',
              width: 200,
              margin: 10,
              marginBottom: 0,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            OLÁ, {usuario}.
          </button>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
            <button
              className="blue-button"
              title="VOLTAR PARA A LISTA DE HOSPTAIS."
              onClick={() => clickVoltar()}
              style={{
                display: 'flex',
                width: 97.5,
                margin: 10,
                marginTop: 5,
                marginRight: 0,
                paddingLeft: 20,
                paddingRight: 20,
              }}
            >
              VOLTAR
            </button>
            <button
              className="blue-button"
              title="FAZER LOGOFF."
              onClick={() => clickLogin()}
              style={{
                display: 'flex',
                width: 97.5,
                margin: 10,
                marginTop: 5,
                marginLeft: 5,
                paddingLeft: 20,
                paddingRight: 20,
              }}
            >
              SAIR
            </button>
          </div>
        </div>
      </div>
    );
  }

  // renderização do componente.
  if (viewUnidades === 1 && cleanrender === 1) {
    return (
      <div
        className="main fade-in"
        style={{
          display: 'flex',
          width: '100%',
          flexDirection: 'column',
        }}
      >
        <Header></Header>
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo} />
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
          <ShowUnidades></ShowUnidades>
          <ViewInterconsultas></ViewInterconsultas>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Unidades;
